    font-family : Lato;
    src         : url('../../resources/fonts/Lato-Thin.woff2') format('woff2'), /* Super Modern Browsers */
    url('../../resources/fonts/Lato-Thin.woff') format('woff'); /* Pretty Modern Browsers */
    font-weight : 100;
}

@font-face {
    font-family : Lato;
    src         : url('../../resources/fonts/Lato-Light.woff2') format('woff2'), /* Super Modern Browsers */
    url('../../resources/fonts/Lato-Light.woff') format('woff'); /* Pretty Modern Browsers */
    font-weight : 300;
}

@font-face {
    font-family : Lato;
    src         : url('../../resources/fonts/Lato-Regular.woff2') format('woff2'), /* Super Modern Browsers */
    url('../../resources/fonts/Lato-Regular.woff') format('woff'); /* Pretty Modern Browsers */
    font-weight : 400;
}

@font-face {
    font-family : Lato;
    src         : url('../../resources/fonts/Lato-Medium.woff2') format('woff2'), /* Super Modern Browsers */
    url('../../resources/fonts/Lato-Medium.woff') format('woff'); /* Pretty Modern Browsers */
    font-weight : 500;
}

@font-face {
    font-family : Lato;
    src         : url('../../resources/fonts/Lato-Bold.woff2') format('woff2'), /* Super Modern Browsers */
    url('../../resources/fonts/Lato-Bold.woff') format('woff'); /* Pretty Modern Browsers */
    font-weight : 700;
}

//endregion

@mixin two-levels-header {
    .demo-header {
        flex-direction : column;
        padding        : 0;
        flex           : 0 0 auto; // Must participate in flex using flex-basis: auto

        #title {
            height : 3em;
        }

        #tools {
            justify-content  : flex-end;
            padding-right    : 1em;
            background-color : rgba(0, 0, 0, .2);
            height           : 5em;
            transition       : background-color 0.4s, margin-top .3s;
        }
    }
}

html, body {
    height   : 100%;
    overflow : hidden;
}

body {
    font-family    : Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
    margin         : 0;
    font-size      : 14px;

    display        : flex;

    flex-direction : column;

    visibility     : hidden;

    &.b-change-size {
        #container {
            transition : all .3s;
        }
    }

    &.b-size-phone {
        display         : flex;
        background      : url(images/woodsmall.jpg) no-repeat center center fixed;
        background-size : cover;

        #container {
            top             : 50%;
            left            : 50%;
            transform       : translate(-50%, -50%);
            width           : 375px;
            height          : 727px;
            max-height      : 727px;
            min-height      : 727px;
            justify-content : center;

            @include two-levels-header();

            #fullscreen-button {
                display : none;
            }

            .b-grid {
                height     : calc(667px - 98px);
                background : #fff;
                max-height : calc(667px - 98px);
                min-height : calc(667px - 98px);
            }

            &::before {
                content             : '';

                position            : absolute;
                width               : 100%;
                height              : calc(100% + 60px);
                max-height          : 707px;
                left                : -10px;
                border              : 10px solid black;
                border-bottom-width : 30px;
                border-top-width    : 30px;
                border-radius       : 20px;
                box-shadow          : 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
            }
        }
    }
}

.b-grid {
    flex : 1;
}

#container {
    position       : relative;

    width          : 100%;
    height         : 100%;
    min-height     : 100%;
    max-height     : 100%;

    flex           : 1;
    display        : flex;
    flex-direction : column;
    transform      : translate(0, 0);
    top            : 0;
    left           : 0;

    &::before {
        content    : '';
        position   : absolute;
        width      : 0;
        height     : 0;
        max-height : 0;
        left       : 0;
        border     : none;
        box-shadow : none;
    }
}

.demo-header {
    display          : flex;
    color            : #fff;
    padding          : 2em 0;
    transition       : background-color .5s;
    background-color : #1E88E5;
    flex-shrink      : 0;

    a {
        color           : inherit;
        text-decoration : none;
    }

    #title-container {
        display     : inline-flex;
        align-items : center;
        flex        : 1;
        min-height  : 3em; // Fix for IE11
    }

    #title {
        display               : inline-flex;
        align-items           : center;
        margin-left           : 1em;
        padding               : 0.2em 0 0.2em 2.1em;
        font-size             : 1.8em;

        background            : url(images/logo_bryntum_bw.png) no-repeat;
        background-size       : 1.6em;
        background-position-y : center;
        white-space           : nowrap;
    }

    #tools {
        display          : flex;
        align-items      : center;
        background-color : inherit;
        transition       : background-color 0s, margin-top .3s;
        padding-right    : 1em;

        .b-widget:not([data-group]) {
            margin-left : .5em;
        }

        .b-button[data-group] {
            margin-left : 1px;
        }

        .b-field {
            margin-right : 1em;
        }

        .b-numberfield {
            width : 18em;
        }
    }
}

.b-hint .header {
    font-weight : bold;
}

.b-notransition .demo-header {
    transition : none;
}

.b-notransition * {
    transition : none !important;
}

@media (max-width : 450px) {
    .demo-header {
        font-size  : calc(.65em + 1vw);
        transition : font-size 0.1s;
    }

    .b-hint {
        display : none;
    }
}

//region Split header by two levels

// Depends on width of the tools index.html (including umd and module versions)
// may contain a div container with one of the following classes:
// `small-sized-tools`, `medium-sized-tools` or `large-sized-tools`
// for example `<div id="container" class="medium-sized-tools"></div>`.
// The class could be omitted. In this case the default max-width value will be used.
// Max width is the browser width value. If actual width less than specified the header gets split by two levels.

// default value
@media (max-width : 370px) {
    @include two-levels-header();
}

@media (max-width : 530px) {
    .small-sized-tools {
        @include two-levels-header();
    }
}

@media (max-width : 750px) {
    .medium-sized-tools {
        @include two-levels-header();
    }
}

@media (max-width : 900px) {
    .large-sized-tools {
        @include two-levels-header();
    }
}

//endregion

#header-tools {
    display : none;
}

.demo-header #header-tools {
    display : inline-block;
}

.info-popup.b-popup {
    .b-popup-content {
        padding : 1.5em;

        .b-html {
            flex-direction : column;
        }

        .header {
            font-weight : 700;
        }

        .description {
            padding-top : .5em;
            line-height : 1.4em;
        }

        .b-widget:not(.b-html) {
            margin-top    : 1em;
            margin-bottom : 0;
        }

        .b-button {
            justify-content : center;
            margin-right    : 1em;
        }
    }
}
